import React, {ReactFragment} from 'react';
import {
    Placeholder,
    Div,
    RichCell,
    Avatar, FixedLayout, CardGrid, Card, Button, CellButton, Textarea
} from "@vkontakte/vkui"
import Icon24UserOutgoing from '@vkontakte/icons/dist/24/user_outgoing';
import bridge from "@vkontakte/vk-bridge";

import {BACKEND} from "../func/func";

class Moder extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            mastersList: []
        };
    }

    componentDidMount() {
        console.log(BACKEND.moder);
        fetch(BACKEND.moder)
            .then(res => res.json())
            .then(mastersList => {
                this.setState({mastersList: mastersList})
            });
    }

    good = (id, index) => {
        let mastersList = this.state.mastersList;
        if (index > -1) {
            mastersList.splice(index, 1);
        } else mastersList.splice(0, index);
        this.setState({mastersList: mastersList});
        let moderation = {
            id: id,
            status: true,
            reasons: []
        };
        fetch(BACKEND.moder, {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, cors, *same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: 'same-origin', // include, *same-origin, omit
            headers: {
                'Content-Type': 'application/json',
            },
            redirect: 'follow', // manual, *follow, error
            referrer: 'no-referrer', // no-referrer, *client
            body: JSON.stringify(moderation), //    body     "Content-Type"
        })
            .then(res => res.json())
            .catch(e=>console.log(e))
    };

    bad = (id, index) => {
        let mastersList = this.state.mastersList;
        if (index > -1) {
            mastersList.splice(index, 1);
        } else mastersList.splice(0, index);
        this.setState({mastersList: mastersList});
        let reasons = [];
        reasons.push(this.state.reason);
        let moderation = {
            id: id,
            status: false,
            reasons: reasons
        };
        fetch(BACKEND.moder, {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, cors, *same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: 'same-origin', // include, *same-origin, omit
            headers: {
                'Content-Type': 'application/json',
            },
            redirect: 'follow', // manual, *follow, error
            referrer: 'no-referrer', // no-referrer, *client
            body: JSON.stringify(moderation), //    body     "Content-Type"
        })
            .then(res => res.json())
            .catch(e=>console.log(e))
    };

    renderMasters() {
        return this.state.mastersList.map((master, index) => {
            return (
                <CardGrid key={master.vkUid} style={{padding: 0}}>
                    <Card size="l" mode="shadow">
                        <RichCell
                            disabled
                            multiline
                            before={<Avatar onClick={() => this.props.openMaster(master._id)} src={master.avatarLink} size={70}/>}
                            caption={master.location.city.title}
                            actions={
                                <React.Fragment>
                                    <Button onClick={()=>this.good(master._id, index)}></Button>
                                    {/*<Button onClick={()=>this.bad(master._id, index)} mode="secondary"></Button>*/}
                                </React.Fragment>
                            }
                        >
                            {master.firstname} {master.lastname}
                        </RichCell>
                    </Card>
                </CardGrid>
            );
        })
    };

    render(){
        return (
                <Div>
                    {this.renderMasters()}
                </Div>
        );
    }
}

export default Moder;